import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import WaterApiList from '../../Components/ApiList/WaterApiList';
import BrandLoader from '../../Components/Common/BrandLoader';
import NoDataFoundStyle1 from '../../Components/Common/NoDataFound/NoDataFoundStyle1';
import AxiosInterceptors from '../../Components/GlobalData/AxiosInterceptors';

const WaterViewAllPaymentHistory = () => {
  const [isLoading, setIsLoading] = useState(false)
  const [fetchedData, setFetchedData] = useState("")
  const [pageNo, setPageNo] = useState(1)
  const [showNoOfPages, setShowNoOfPages] = useState(5)

  const { api_waterCitizenPaymentHistory, header } = WaterApiList();

  const navigate = useNavigate()

  useEffect(() => {
    fetchTransactionHistory()
  }, [pageNo, showNoOfPages])

  const fetchTransactionHistory = () => {
    setIsLoading(true)

    AxiosInterceptors.post(api_waterCitizenPaymentHistory, { "pages": showNoOfPages, "page": pageNo }, header)
      .then((res) => {
        if (res.data.status) {
          console.log('Water Citizen payment history date', res.data.data)
          setFetchedData(res.data.data)
          setIsLoading(false)
        } else {
          console.log('Water Citizen payment history date', res.data.data)
          setIsLoading(false)
        }
      })
      .catch((error) => {
        console.log('Exception while getting water payment history', error)
        setIsLoading(false)
      })
  }

  const conditionalNavigate = (transNo, transId) => { // trans id => 2 = Consumer Receipt, 2 = Application Receipt(Connection, site inspection etc), 
    console.log("transId",transNo, transId)
    if (transId == 1) return navigate(`/waterUserReceipt/${transNo}`)
    if (transId == 2) return navigate(`/waterConnReceipt/${transNo}`)
  }


  const filterLabelData = (label) => {
    if (label == "&laquo; Previous") return "<< Prev";
    if (label == "Next &raquo;") return ">> Next";
    return label;
  }

  if (isLoading) return <BrandLoader />;
  if (fetchedData?.data?.length < 1) return <NoDataFoundStyle1 message="No Payment History Found." />


  if (fetchedData?.data?.length >= 1) return (
    <>
      <div className='my-3 font-semibold text-center text-lg text-gray-700'> Water Payment Transactions </div>



      <div className="relative overflow-x-auto sm:rounded-lg border shadow-md">

        <div className='flex justify-between my-3 mx-5'>
          <div className='flex gap-3'>
            <p>Show</p>
            <select onChange={(e) => setShowNoOfPages(e.target.value)} name="" id="" className='border rounded shadow w-24'>
              <option value="5">5 Records</option>
              <option value="10">10 Records</option>
              <option value="20">20 Records</option>
              <option value="30">30 Records</option>
            </select>
          </div>
          <div className='flex gap-3'>
            <p>Search</p>
            <input type="text" name="search" id="" className='border rounded shadow w-40' />
          </div>
        </div>

        <table className="w-full text-sm text-left text-gray-500 ">
          <thead className="text-xs text-gray-700 uppercase bg-gray-50">
            <tr>
              <th scope="col" className="p-4"> <div className="flex items-center"> SL </div> </th>
              <th scope="col" className="px-6 py-3">Transaction No.</th>
              <th scope="col" className="px-6 py-3">Payment Mode</th>
              <th scope="col" className="px-6 py-3">Amount</th>
              <th scope="col" className="px-6 py-3">Paid For</th>
              <th scope="col" className="px-6 py-3">Transaction Date</th>
              <th scope="col" className="px-6 py-3">Action</th>
            </tr>
          </thead>
          <tbody>

            {fetchedData && fetchedData?.data?.map((item, index) => (

              <tr className="bg-white border-b dark:bg-gray-800 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-600">
                <td className="w-4 p-4">
                  <div className="flex items-center">{index + 1}</div>
                </td>
                <th scope="row" className="px-6 py-4 font-medium text-gray-900 whitespace-nowrap dark:text-white">{item.tran_no}</th>
                <td className="px-6 py-4">{item.payment_mode}</td>
                <td className="px-6 py-4">{item.amount}</td>
                <td className="px-6 py-4">{item.tran_type}</td>
                <td className="px-6 py-4">{item.tran_date}</td>
                <td className="px-6 py-4">
                  <p onClick={() => conditionalNavigate(item.tran_no, item.tran_type_id)} className="font-medium text-blue-600 border-indigo-600 cursor-pointer hover:bg-indigo-600 hover:text-white shadow-md rounded border px-2 py-1 text-center whitespace-nowrap">View Receipt</p>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        <nav className="block md:flex items-center md:justify-between text-center md:pt-4 m-5" aria-label="Table navigation">
         
          <span className="text-sm font-normal text-gray-500">Showing <span className="font-semibold text-gray-900 dark:text-white">{fetchedData?.from}-{fetchedData?.to}</span> of <span className="font-semibold text-gray-900 dark:text-white">{fetchedData?.total}</span></span>
          <ul className="inline-flex items-center -space-x-px">


            {fetchedData?.links?.map((item) => (
              <li>
                <p onClick={() => { setPageNo(item?.url.slice(-1)) }} className={` ${item?.active == true ? "bg-indigo-500 text-white" : "bg-white text-gray-500"} px-3 py-2 leading-tight  border border-gray-300 hover:bg-indigo-100 hover:text-gray-700 cursor-pointer whitespace-nowrap`}>{filterLabelData(item.label)}</p>
              </li>
            ))
            }

          </ul>
        </nav>
      </div>


    </>
  )
}

export default WaterViewAllPaymentHistory
